html, body, body > div {
  width: 100%;
  height: 100%;
  background: radial-gradient(#000, #111, #222, #333, #444, #555, #666, #777, #888, #999, #AAA);
  margin: 0;
}

* {
  color: white;
  user-select: none;
}

.game-canvas {
  position: relative;
  left: 5%;
  width: 95%;
  height: 100%;
  background-color: #666;
}

.sidebar button {
  border: none;
  background: none;
  border-radius: 10px;
}

.on {
  background-color: lightgreen;
  color: green;
}

button.opened {
  color: lightblue;
}

button.opened::before {
  content: ">>"
}


.sidebar > *:hover {
  animation: lightShake 200ms 1;
}

@keyframes lightShake {
  25% {
    transform: rotateZ(5deg);
  } 50 % {
    transform: rotateZ(-5deg);
  } 75% {
    transform: rotateZ(5deg);
  }
}

.about {
  font-size: 12px;
}